.live-diagram {
  margin: 200px auto 0;
  width: 1000px;
  .diagram-disk {
    background-color: #fff;
    background-image:
      radial-gradient(
        closest-side,
        transparent,
        transparent 50%,
        rgba(255, 255, 255, 0.12) 70%,
        rgba(255, 255, 255, 0.4)), 
      conic-gradient(
        from 270deg,
        #ff811f 45deg,
        #9000d8 45deg 135deg,
        #0d77cd 135deg 180deg,
        transparent 180deg);
    border-radius: 50%;
    height: 816px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    width: 816px;
    .outer-sector {
      border-radius: 50% / 43%;
      // box-shadow: 0 16px 10px -17px rgba(0,0,0,0.5);
      height: 440px;
      left: 119px;
      margin: 0 auto;
      overflow: hidden;
      position: absolute;
      top: 50%;
      transform: rotateZ(137.35deg);
      transform-origin: top center;
      width: 578px;
      &::after {
        background-color: #ee5bbc;
        background-image: linear-gradient(310deg, #ee5bbc 0%, #ee5bbc 60%, #ff3786 80%, #ff3786 100%);
        border-left: 2px solid rgba(255, 255, 255, .45);
        border-top: 2px solid rgba(255, 255, 255, .45);
        // box-shadow: -1px -1px 10px -2px rgba(0,0,0,0.5);
        content: '';
        height: 408px;
        left: 84px;
        position: absolute;
        top: 216px;
        transform: rotate(45deg) skew(24.5deg, 24.5deg);
        width: 408px;
      }
      .outer-sector_edge {
        border-top: 32px solid #9b106f;
        border-image: linear-gradient(to top, #9b106f, #fd4169);
        height: 0;
        position: absolute;
        transform-origin: bottom right;
        width: 0;
        &:first-child {
          border-left: 18px solid #fd4169;
          clip-path: polygon(0 0, 100% 0, 0 100%);
          left: 442px;
          top: 373px;
          transform: rotateZ(-21deg);
        }
        &:nth-child(2) {
          border-right: 18px solid #fd4169;
          clip-path: polygon(0 0, 100% 0, 100% 100%);
          left: 117px;
          top: 379px;
          transform: rotateZ(21deg);
        }
      }
      &:nth-child(2) {
        height: 434px;
        transform: rotateZ(224.75deg);
        &::after {
          background-image: linear-gradient(310deg, #9dce46 0%, #9dce46 60%, #7bcc62 80%, #7bcc62 100%);
          top: 257px;
          transform: rotate(45deg) skew(30.75deg, 30.75deg);
        }
        .outer-sector_edge {
          border-top: 27px solid #158127;
          border-image: linear-gradient(to top, #158127, #a0d33d);
          transform-origin: bottom left;
          &:first-child {
            border-left: 15px solid #a0d33d;
            clip-path: polygon(0 0, 100% 0, 0 100%);
            left: 396px;
            top: 394px;
            transform: rotateZ(-15deg);
          }
          &:nth-child(2) {
            border-right: 15px solid #a0d33d;
            clip-path: polygon(0 0, 100% 0, 100% 100%);
            left: 168px;
            top: 390px;
            transform: rotateZ(15deg);
          }
        }
      }
    }
  }
}
